import React, { useEffect } from 'react';
import krx from 'krx-stock-api';
 
const Home = () => {
    useEffect( async () => {
        try{
            const stock = await krx.getStock('005930');
            console.log(stock);
        } catch(e){
            console.log(e);
        }
    }, [])

    return(
        <div>Home</div>
    )
}

export default Home;